.auth-scheme-detail-container .tabs-header .el-radio-button,
.auth-scheme-detail-container .tabs-header .el-radio-button__inner {
  width: 100%;
}
.auth-scheme-detail-container .tabs-header .is-active .el-radio-button__inner {
  background-color: #E6E6E6 !important;
}
.auth-scheme-detail-container .tabs-header .el-radio-button__inner {
  background-color: #F5F5F5 !important;
  color: #333 !important;
  border-color: #CCCCCC !important;
  box-shadow: -1px 0 0 0 #CCCCCC !important;
}
.auth-scheme-detail-container .el-radio + .el-radio {
  margin-left: 0;
}
.auth-scheme-detail-container .el-collapse-item__header {
  height: auto;
  background-color: transparent;
  padding-bottom: 10px;
}
.auth-scheme-detail-container .el-radio__inner {
  border: none;
  background: url("../../../../assets/img/icons/radio-unchecked.png") no-repeat !important;
  background-size: 100% !important;
}
.auth-scheme-detail-container .el-radio__input.is-checked .el-radio__inner {
  background-image: url("../../../../assets/img/icons/radio-checked.png") !important;
}
.auth-scheme-detail-container .el-radio__input.is-checked .el-radio__inner:after {
  display: none;
}
